/* App.css */

/* Background color for the page */
body {
  background-color: #f7f7f7; /* Light gray color */
}

/* Centering the content */
.center-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

/* Heading Styles */
.app-heading {
  font-size: 32px;
  margin-bottom: 16px;
  background-color: #007bff; /* Blue color for the heading */
  color: #fff; /* White text color */
  padding: 10px 20px;
  border-radius: 4px;
}

/* Paragraph Styles */
.app-paragraph {
  font-size: 18px;
  margin-bottom: 16px;
}

/* Dropdown Button Styles */
.dropdown-button {
  background-color: #f2f2f2;
  color: black;
  border: none;
  cursor: pointer;
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 4px;
}

.dropdown-button:hover {
  background-color: #ddd;
}

/* Dropdown List Styles */
.dropdown-list {
  width: 200px; /* Adjust as needed */
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  margin-top: 16px;
}

/* Selected Message Styles */
.selected-message {
  font-size: 18px;
  margin-top: 16px;
}
